.container{
    max-width: 100%;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
    
}

.section{
    padding: 60px 0;
}  

@media screen and (min-width: 480px) {
    .container{
        width: 480px;
    }
}
@media screen and (min-width: 768px) {
    .container{
        width: 768px;
    }
}
@media screen and (min-width: 1200px)   {
    .container{
        width: 1200px;
    }
    .section{
        padding: 94px 0;
    }
}

 